//client/src/pages/Login.js
import React, { useState } from "react";
import { Card, Row } from "react-bootstrap";
import { useNavigate } from "react-router-dom";

import CryptoJS from "crypto-js"; // 
import Swal from "sweetalert2"; // alert 
import axios from "axios";

import Narvar from "../MapPage/Narvar";
import "./button.css";

const url = "http://localhost:8080/api/login";

function LoginPage(props) {
  const navigate = useNavigate();
  const [Email, setEmail] = useState("");
  const [Password, setPassword] = useState("");

  const onEmailHandler = (event) => {
    setEmail(event.currentTarget.value);
  };

  const onPasswordHandler = (event) => {
    setPassword(event.currentTarget.value);
  };

  const onSubmitHandler = (event) => {
    event.preventDefault();
  };

  const onClickLogin = (event) => {
    const secretKey = "0509";
    console.log("click login");
    const data = {
      email: Email,
    };

    axios
      .post(url, JSON.stringify(data), {
        //   
        headers: { "Content-Type": "application/json" },
      })
      .then((res) => {
        console.log(res);
        //   
        if (res.data == "not found") {
          new Swal({
            title: "  .",
            icon: "warning",
          });
        } else {
          //  
          const bytes = CryptoJS.AES.decrypt(res.data, secretKey);
          const original = bytes.toString(CryptoJS.enc.Utf8);
          if (original == Password) {
            //  
            new Swal({
              title: " !",
              icon: "success",
            }).then(function () {
              navigate("/");
            });
          } else {
            //   
            new Swal({
              title: "  .",
              icon: "warning",
            });
          }
        }
      })
      .catch((error) => {
        console.log(error);
      });
    console.log("test");
  };

  return (
    <div>
      <Narvar></Narvar>
      <div
        style={{
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
          width: "100%",
          height: "100vh",
        }}
      >
        <Row>
          <h2></h2>
          <Card body style={{ marginTop: "1rem", borderRadius: "10px" }}>
            <br />
            <form
              style={{ display: "flex", flexDirection: "column" }}
              onSubmit={onSubmitHandler}
            >
              <lable></lable>
              <input type="text" value={Email} onChange={onEmailHandler} />
              <br />
              <lable></lable>
              <input
                type="Password"
                value={Password}
                onChange={onPasswordHandler}
              />
              <br />
              <pre>
                <a href="http://localhost:3000/register"></a>{" "}
                <a href="http://localhost:3000/findPwd"> </a>
              </pre>
              <button className="button" type="submit" onClick={onClickLogin}>
                
              </button>
            </form>
          </Card>
        </Row>
      </div>
    </div>
  );
}
export default LoginPage;
